* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Poppins", sans-serif;
	text-decoration: none;
	list-style: none;
	scroll-behavior: smooth;
 }
 :root {
	--bg-color: #222327;
	/* --bg-color: #2a2a2a; */
	/* --second-bg-color: #202020; */
	--second-bg-color: #121214;
	--text-color: #fff;
	--second-color: #ccc;
	--main-color: #f1024d;
	--big-font: 5rem;
	--h2-font: 3rem;
	--p-font: 1.1rem;
 }
 body {
	background: var(--bg-color);
	color: var(--text-color);
 }
 header {
	position: fixed;
	width: 100%;
	top: 0;
	right: 0;
	z-index: 1000;
	display: flex;
	align-items: center;
	justify-content: space-between;
	background: transparent;
	padding: 22px 15%;
	border-bottom: 1px solid transparent;
	transition: all 0.45s ease;
 }
 .logo {
	color: var(--text-color);
	font-size: 35px;
	font-weight: 700;
	letter-spacing: 1px;
 }
 span {
	color: var(--main-color);
 }
 .navlist {
	display: flex;
 }
 .navlist a {
	color: var(--second-color);
	font-size: 17px;
	font-weight: 500;
	margin: 0 25px;
	transition: all 0.45s ease;
 }
 .navlist a:hover {
	color: var(--main-color);
 }
 .navlist a.active {
	color: var(--main-color);
 }
 #menu-icon {
	font-size: 35px;
	color: var(--text-color);
	z-index: 10001;
	cursor: pointer;
	margin-left: 25px;
	display: none;
 }
 
 section {
	padding: 160px 15% 120px;
 }
#background-video{
	width: 100vw;
	height: 100vh;
	object-fit: cover;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: -1;
}
 .home {
	position: relative;
	height: 100vh;
	width: 100%;
	background-image: url(./Assets/background.png);
	background-size: cover;
	background-position: center;
	display: flex;
	align-items: center;
	justify-content: flex-start;
 }
 .slide {
	margin-bottom: 20px;
 }
 .one {
	display: inline-block;
	margin-right: 15px;
	height: 32px;
	padding: 0 15px;
	line-height: 32px;
	font-size: 20px;
	font-weight: 500;
	border-radius: 5px;
	background: var(--main-color);
	color: var(--text-color);
 }
 .two {
	display: inline-block;
	color: var(--second-color);
	font-size: 20px;
	font-weight: 500;
 }
 .home-text h1 {
	font-size: var(--big-font);
	font-weight: 700;
	color: var(--text-color);
	line-height: 1.1;
	margin: 0 0 8px;
 }
 .home-text h3 {
	color: var(--text-color);
	margin: 0 0 35px;
	font-size: 3rem;
	font-weight: 700;
	line-height: 1.1;
 }
 .home-text p {
	color: var(--second-color);
	font-size: var(--p-font);
	line-height: 1.8;
	margin-bottom: 40px;
 }
 .button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
 }
 .btn {
	display: inline-block;
	padding: 12px 28px;
	background: var(--main-color);
	border-radius: 5px;
	color: var(--text-color);
	font-size: 1rem;
	letter-spacing: 1px;
	font-weight: 600;
	transition: all 0.45s ease;
 }
 .btn:hover {
	transform: scale(0.9);
 }
 .btn2 {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	font-size: 15px;
	font-weight: 600;
	color: var(--text-color);
	margin-left: 35px;
 }
 .btn2 span i {
	height: 55px;
	width: 55px;
	background: var(--main-color);
	color: var(--text-color);
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 35px;
	border-radius: 50%;
	margin-right: 15px;
	transition: all 0.45s ease;
 }
 .btn2 span i:hover {
	transform: scale(1.1) translateY(5px);
 }
 
 header.sticky {
	background: var(--bg-color);
	border-bottom: 1px solid #ffffff1a;
	padding: 12px 15%;
 }
 
 .about {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	align-items: flex-start;
	gap: 1.5rem;
 }
 .about-img img {
	max-width: 530px;
	height: auto;
	width: 100%;
	border-radius: 8px;
 }
 .about-text h2 {
	font-size: var(--h2-font);
	line-height: 1;
 }
 .about-text h4 {
	font-size: 24px;
	font-weight: 600;
	color: var(--text-color);
	line-height: 1.7;
	margin: 15px 0 30px;
 }
 .about-text p {
	color: var(--second-color);
	font-size: var(--p-font);
	line-height: 1.8;
	margin-bottom: 4rem;
 }
 
 .services {
	background: var(--second-bg-color);
 }
 
 .service-box:hover .bx {
	transition: transform 0.8s;
	transform: rotateY(360deg);
 }
 
 @keyframes bounce-skills {
	0% {
	  transform: translate(0);
	}
	20% {
	  transform: translateY(-6px);
	}
	40% {
	  transform: translate(0);
	}
	60% {
	  transform: translateY(-3px);
	}
	80% {
	  transform: translate(0);
	}
 }
 
 .main-text {
	text-align: center;
 }
 .main-text p {
	color: var(--second-color);
	font-size: 15px;
	margin-bottom: 15px;
 }
 .main-text h2 {
	font-size: var(--h2-font);
	line-height: 1;
 }
 .services-content {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, auto));
	align-items: flex-end;
	gap: 2rem;
	margin-top: 5rem;
 }
 .box {
	background: var(--bg-color);
	padding: 35px 45px;
	border-radius: 8px;
	transition: all 0.45s ease;
	outline: 3px solid transparent;
	outline-offset: 4px;
	/* outline-width: 3px;
	outline-color: var(--main-color); */
 }
 .s-icons i {
	font-size: 32px;
	margin-bottom: 20px;
 }
 .box h3 {
	font-size: 24px;
	font-weight: 600;
	color: var(--text-color);
	margin-bottom: 15px;
 }
 .box p {
	color: var(--second-color);
	font-size: 1rem;
	line-height: 1.8;
	margin-bottom: 25px;
 }
 .read {
	display: inline-block;
	padding: 8px 18px;
	background: #333333;
	color: var(--second-color);
	font-size: 14px;
	font-weight: 500;
	border-radius: 8px;
	transition: all 0.45s ease;
 }
 .read:hover {
	letter-spacing: 1px;
	background: #ffffff99;
	color: var(--text-color);
 }
 .box:hover {
	transform: translateY(8px);
	cursor: pointer;
	outline: 3px solid var(--main-color);
 }
 
 .portfolio-content {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(320px, auto));
	align-items: center;
	gap: 2.5rem;
	margin-top: 5rem;
 }
 .row {
	position: relative;
	overflow: hidden;
	border-radius: 8px;
	cursor: pointer;
 }
 .row img {
	width: 100%;
	border-radius: 8px;
	display: block;
	transition: transform 0.5s;
 }
 .layer {
	width: 100%;
	height: 0;
	background: linear-gradient(#ed467b88, var(--main-color));
	position: absolute;
	border-radius: 8px;
	left: 0;
	bottom: 0;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 0 24px;
	transition: height 0.5s;
 }
 .layer h5 {
	font-size: 20px;
	font-weight: 600;
	margin-bottom: 15px;
 }
 .layer p {
	color: rgb(225, 225, 225);
	/* color: var(--second-color); */
	font-size: 0.9rem;
	/* line-height: 1.8; */
 }
 .layer i {
	color: var(--main-color);
	margin-top: 12px;
	font-size: 16px;
	background: var(--text-color);
	width: 40px;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
 }
 .row:hover img {
	transform: scale(1.1);
 }
 .row:hover .layer {
	height: 100%;
 }
 
 .contact {
	background: var(--second-bg-color);
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	align-items: flex-start;
	gap: 2.5rem;
 }
 .contact-text h4 {
	color: var(--text-color);
	margin: 18px 0;
	font-size: 20px;
	font-weight: 600;
 }
 .contact-text h2 {
	font-size: var(--h2-font);
	line-height: 1;
 }
 .contact-text p {
	color: var(--second-color);
	font-size: var(--p-font);
	line-height: 1.8;
	margin-bottom: 30px;
 }
 .list {
	margin-bottom: 2.8rem;
 }
 .list li {
	margin-bottom: 12px;
 }
 .list li a {
	display: block;
	color: var(--second-color);
	font-size: 14px;
	transition: all 0.45s ease;
 }
 .list li a:hover {
	color: var(--text-color);
	transform: translateX(5px);
 }
 .contact-icons i {
	height: 45px;
	width: 45px;
	background: var(--main-color);
	color: var(--text-color);
	font-size: 22px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	border-radius: 8px;
	margin-right: 15px;
	transition: all 0.45s ease;
 }
 .contact-icons i:hover {
	background: var(--text-color);
	color: var(--main-color);
 }
 
 .contact-form form {
	position: relative;
 }
 .contact-form form input,
 form textarea {
	width: 100%;
	padding: 14px;
	background: var(--bg-color);
	color: var(--text-color);
	border: none;
	outline: none;
	font-size: 15px;
	border-radius: 8px;
	margin-bottom: 10px;
 }
 .contact-form textarea {
	resize: none;
	height: 240px;
 }
 .contact-form .submit {
	display: inline-block;
	font-size: 16px;
	background: var(--main-color);
	color: var(--text-color);
	width: 160px;
	transition: all 0.45s ease;
 }
 .contact-form .submit:hover {
	transform: scale(1.1);
	cursor: pointer;
 }
 
 .end {
	padding: 20px 15%;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	gap: 2rem;
	background: var(--bg-color);
 }
 .last-text p {
	color: var(--second-color);
	font-size: 14px;
 }
 .top i {
	padding: 10px;
	border-radius: 8px;
	font-size: 15px;
	color: var(--text-color);
	background: var(--main-color);
 }
 
 .submit-msg {
	position: fixed;
	margin-bottom: 8px;
 }
 
 .submit-msg--success {
	color: #16c60c;
 }
 
 .submit-msg--error {
	color: red;
 }
 
 @media (max-width: 1480px) {
	header {
	  padding: 12px 2.5%;
	  transition: 0.1s;
	}
	header.sticky {
	  padding: 10px 2.5%;
	  transition: 0.1s;
	}
	section {
	  padding: 110px 3% 60px;
	}
	.end {
	  padding: 15px 3%;
	}
 }
 
 @media (max-width: 1100px) {
	:root {
	  --big-font: 4rem;
	  --h2-font: 2.5rem;
	  --p-font: 1rem;
	  transition: 0.1s;
	}
	.home-text h3 {
	  font-size: 2.5rem;
	}
	.home {
	  height: 87vh;
	}
 }
 
 @media (max-width: 920px) {
	.about {
	  grid-template-columns: 1fr;
	}
	.about-img {
	  text-align: center;
	  order: 2;
	}
	.about-img img {
	  width: 100%;
	  height: auto;
	  max-width: 100%;
	}
	.contact {
	  grid-template-columns: 1fr;
	}
 }
 
 @media (max-width: 840px) {
	#menu-icon {
	  display: block;
	}
	.navlist {
	  position: absolute;
	  top: -1000px;
	  right: 0;
	  left: 0;
	  display: flex;
	  flex-direction: column;
	  background: var(--bg-color);
	  text-align: left;
	  transition: all 0.45s ease;
	}
	.navlist a {
	  display: block;
	  margin: 17px;
	  font-size: 20px;
	  transition: all 0.45s ease;
	  color: var(--text-color);
	}
	.navlist a:hover {
	  color: var(--main-color);
	}
	.navlist a.active {
	  color: var(--text-color);
	}
	.navlist.open {
	  top: 100%;
	}
 }
 
 .mockup-by {
	color: inherit;
	font-size: 0.8em;
	font-weight: 100;
 }
/*=============== SCROLL BAR ===============*/
::-webkit-scrollbar {
  width: 0.6rem;
  border-radius: 0.5rem;
  background-color: hsl(207, 4%, 15%);
}

::-webkit-scrollbar-thumb {
  background-color: #d20043;
  border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--main-color);
}
